            <ul class="isaktif sm:pl-4 anim mb-3 text-white animated slideInRight " style="background:#232E30;" v-show="menuTrigger">
              <li class="bord hover:bg-orange-400 text-left sm:pl-3" style="font-size:13px;" @click="$router.push('/lembur')">&nbsp;&nbsp;Menu Utama&nbsp;&nbsp;<span class="typcn typcn-media-eject"></span></li>
              <li class="bord hover:bg-orange-400 text-left sm:pl-3" style="font-size:13px;" @click="$router.push('/admin')">&nbsp;&nbsp;Admin&nbsp;&nbsp;<span class="typcn typcn-user"></span></li>
            </ul> -->
