              <div class="mt-2 mb-2" v-show="menuTrigger">
              <v-select :options="kategoris" label="kategori" v-model="carikategori" :reduce="e => e.id"></v-select>
              </div>
              
              <button type="button"  class="btn btn-sm btn-dark  " @click="cari(`/app/shop/search?kategori=${carikategori}`)" v-show="menuTrigger">Cari</button>
