        >
          <div class="row">
            <!-- RIGHT TOP -->
            <div class="col-sm-12 bg-white" style="padding:0;margin:0;position:fixed;z-index:100;">
                <div class="row justify-content-between align-items-center shadow rounded-lg" style="height:50px;padding:3px;margin-left:0px;">
                    <div class="col-2 ">
                       
                        <span class="typcn typcn-th-menu text-2xl d-inline-block d-sm-none" @click="menuTrigger=!menuTrigger"></span>
                              
                        <div class="d-none d-sm-inline-block">
                            <span class="font-bold text-md text-orange-500 italic" @click="$router.push('/app/shop')">Hello There</span>
                        </div>
                    </div>
                  <div class="col-6">
                    <button @click="cari(`/app/shop/search?s=${name}`)" type="button" class="btn btn-sm btn-dark rounded-circle"
                    style="position:absolute;z-index:1000;right:20px;top:4px;"
                    ><span class="typcn typcn-zoom"></span></button>
                     <b-autocomplete
                            rounded
                            v-model="name"
                            :data="filteredDataArray"
                            placeholder="search"
                            icon="magnify"
                            clearable
                            @select="option => selected = option">
                            <template #empty>No results found</template>
                    </b-autocomplete>
                      <!-- <div class="sm-form ">
                          <input type="text" id="search" name="search" class="rounded-xl form-control form-control-sm" placeholder="search ..." v-model="search"  >
                      </div> -->
                    <!-- <div class="input-group ">
                        <div class="input-group-prepend">
                            <div class="input-group-text"><span class="typcn typcn-zoom"></span></div>
                        </div>
                        <input type="text" class="form-control py-0" id="search" placeholder="search ...">
                    </div> -->
                  </div>
                  <div class="col-4 row justify-content-center">
