                      </div>
                      <div class="col-2 d-none d-sm-inline-block" style="position:relative;">
                          <router-link to="/app/shop/cart">
                          <span style="position:absolute;top:-13px;" class="font-bold text-sm p-0 pl-1 pr-1 m-1 btn btn-danger btn-sm rounded-circle" v-if="$store.state.keranjang>0">{{$store.state.keranjang}}</span>
                            <span class="text-lg typcn typcn-shopping-cart"></span>
                          </router-link>
                      </div>
