<script>
import axios from 'axios'
import firebase from 'firebase'
let db = firebase.firestore()
import autophp from '@/plugins/autophp_local.js'
let sdb = new autophp()
var id = require("date-fns/locale/id");
export default {
  components: {
  },
  data () {
    return {
      bgleft1: '#fff',
      test: 1,
      menuTrigger: false,
      menuKanan: false,
      busy: false, // digunakan untuk melihat apakah ada data yang masih diprocess untuk dimunculkan loading
      user:false,
      keranjang:0,
      harga1:0,
      harga2:0,
      kategoris:[],
      carikategori:'',
      search:'',
      name:'',
      produks: [
          'Angular',
      ],
    }
  },
   beforeMount() {
      this.getKategori();
      let that = this;
    //   socket.on("new-message", message => {
    //   if(message.target_id==that.$store.state.users.id && message.app=='driver'){
    //       that.refreshNotif()
    //        //  CORDOVA
    //       document.addEventListener('deviceready', function () {
    //           cordova.plugins.notification.local.schedule({
    //               title: 'BTN Banjarbaru',
    //               text: message.text,
    //               sound:'file://resources/audio/beep.mp3'
    //           });
    //           cordova.plugins.notification.local.on('click', function (notification, eopts) { 
    //               setTimeout(() => {
    //                   that.$router.push(message.link)
    //               }, 1000);
    //           });
    //           navigator.notification.beep(1);
    //       })
    //   }
    // });
  },
  computed:{
     filteredDataArray() {
                return this.produks.filter((option) => {
                    return option
                        .toString()
                        .toLowerCase()
                        .indexOf(this.name.toLowerCase()) >= 0
                })
            }
  },
  methods: {
    cari(item){
      this.$router.push(item)
      setTimeout(() => {
        this.$router.go()
      }, 1000);
    },
    exits(){
      document.addEventListener("deviceready", function(e){
        navigator.app.exitApp();
      }, true);
    }, 
    logout () {
      this.$store.dispatch('logout')
      localStorage.removeItem('login')
      this.$router.push('/app/kasir')
    },
    muncul (e) {
      e.target.nextElementSibling.classList.toggle('isaktif')
    },
    parent(e){
      e.target.parentNode.click();
    },
    onSwipeRight () {
      this.menuTrigger = true
    },
    getKeranjang(){
       sdb.collection("app_kasir_keranjang").doc().select(`select tb1.id,tb2.nama_produk,tb1.harga,tb1.diskon,tb1.jumlah,tb1.total from app_kasir_keranjang tb1 left join app_kasir_produk tb2 on tb2.id=tb1.id_produk where tb1.id_user='${this.$store.state.users.id}' AND tb1.posisi='keranjang' AND tb1.beli='online'`).then(res => {
            this.$store.state.keranjang=res.length
            this.$forceUpdate();
        });
    },
    getKategori(){
      sdb.collection("app_kasir_produk_kategori").doc().get("*","order by id").then(res => {
          this.kategoris=res;
          this.$forceUpdate();
      });
    
    },
    getListProduk(){
      sdb.collection("app_kasir_produk").doc().get("nama_produk","order by id").then(res => {
          this.produks=res.map(e=>e.nama_produk);
          console.log(res)
          this.$forceUpdate();
      });
      
    }
  },
  mounted () {
    let that=this
    this.getListProduk();
    setTimeout(() => {
     this.getKeranjang();
    }, 3000);
    firebase.auth().onAuthStateChanged((users) => {
          if (users) {
              let user = {
                  uid: users.uid,
                  displayName: users.displayName,
                  name: users.displayName,
                  email: users.email,
                  emailVerified: users.emailVerified,
                  gambar:users.photoURL,
                  level:'user',
                  auth:'google'
              };
              firebase.auth().currentUser.getIdToken(/* forceRefresh */ true).then(function(idToken) {
                  localStorage.setItem("auth._token.local", 'bearer '+idToken);
                  localStorage.setItem("auth.local", 'bearer '+idToken);
                  }).catch(function(error) {
              });
              this.$store.commit("changeUsers", user);
              localStorage.setItem("users", JSON.stringify(user));
              //doc adalah select *
              sdb.collection("tbuser").doc().get("*",`where uid='${users.uid}' order by id`).then(res => {
                  that.$store.state.users.id=res[0].id
                  // sdb.collection("app_kasir_transaksi_nota").doc().select(`
                  // select * from app_kasir_transaksi_nota where id_user='${res[0].id}' AND status='4'`).then(res => {
                  //     if(res.length>0){
                  //       that.$router.push('/app/shop/transaksi/bayar')
                  //     }
                  // });
                  
                  that.$forceUpdate();
              });
              
          } else {
              console.log('user sesion tidak ada')
          }
      });
  },
}
</script>
<style scoped>
.isaktif {
  transition: all 0.3s ease-in-out;
  display: none;
}
